@use '../../../styles/variables' as *;
@use '../../../styles/mixins' as mixins;

.discussionWidget {
  background-color: $white;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em,
    rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  cursor: pointer;
  background-color: $lighter-grey;
  border-radius: 8px;
  padding: 1.6rem;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: $GREY200;
  }

  h5 {
    color: $dark-grey;
    border-bottom: 2px solid $light-grey;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    text-align: right;
  }

  .widgetContent {
    width: 100%;

    .widgetItem {
      padding: 8px 0;
      border-bottom: 1px solid $medium-grey;

      &:last-child {
        border-bottom: none;
      }

      .itemDetails {
        display: flex;
        flex-direction: column;

        .itemName {
          font-weight: bold;
          font-size: 1.6rem;
          color: $MAIN900;
          margin-bottom: 0.5rem;
        }

        .itemAuthor {
          font-size: 1.6rem;
          color: $medium-grey;
          margin-bottom: 0.5rem;
        }

        .itemMessage {
          font-size: 1.6rem;
          color: $dark-grey;
          margin-bottom: 1.75rem;
        }

        .itemTime {
          font-size: 1.4rem;
          color: $medium-grey;
          align-self: flex-end;
          font-weight: bold;
        }
      }
    }

    .viewMoreButton {
      margin-top: 12px;
      color: $MAIN900;
      background: none;
      border: none;
      cursor: pointer;
      text-decoration: underline;
    }
  }
}
